.mobile-overlay {
    border-image-source: linear-gradient(90deg, #2600fc 0%, #ff00ea 100%);
    border-width: 4px 0 0;
    border-image-slice: 1;

    background-color: black;

    height: 100%;
    width: 100%;

    &__header {
        padding: 30px 25px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: 1px solid var(--border-color-gray);
    }

    &__menu {
        &-list {

        }

        &-item {
            padding: 10px 230px 10px 25px;
        }
    }

    &__close-button {
        width: 100%;
        height: 100%;
        aspect-ratio: 1;
        position: relative;
        padding: 0;
        background-color: transparent;

        &::before {
            rotate: -45deg;
        }

        &::after {
            rotate: 45deg;
        }

        &::before, &::after {
            
            content: "";
            position: absolute;
            top: 50%;
            left: 5%;
            width: 90%;
            height: 1px;
            background-color: #741FF5;
            border-radius: 5px;
        }

        &-wrapper {
            width: 10%;
        }

    }
}